import React from 'react';

import Layout from '../components/Layout';

const IndexPage = () => (
  <Layout>
    <div id="main">
      <div className="box container">
        <header>
          <h2>Truyện ngắn</h2>
        </header>
        <section>
          <header>
            <h3>Phước là tôi</h3>
          </header>
          <blockquote>
            FAMILY is not an important thing, it's EVERYTHING.
          </blockquote>
        </section>
        <section>
          <header>
            <h3>Phước là tui</h3>
          </header>
          <p>
            Tôi tên là Phước, developer cũng như là một programming teacher,
            cũng khá may mắn khi tôi có thể theo đuổi hai niềm đam mê lẫn hứng
            thú của mình ở thời điểm hiện tại. Việc theo đuổi Edu khá bất ngờ,
            nhưng thực sự cảm thấy có ý nghĩa. Tất nhiên là tôi vẫn phải dành
            thời gian self-study cả về tech lẫn mọi thứ liên quan.
          </p>
          <hr />
          <p>
            Thật may mắn khi tôi được nhận vào Fsoft làm fresher khi còn chưa
            tốt nghiệp, quả thực một môi trường doanh nghiệp phần mềm lớn đã
            mang lại rất nhiều điều bổ ích cho tôi cùng những người thầy, người
            đồng nghiệp đáng mến. Điều đó đã giúp tôi tự tin và trưởng thành
            hơn. Tôi trân trọng quãng thời gian ngắn ngủi với FCS, nhưng thật
            tiếc lúc đó tôi không đủ vững để tiếp tục ở lại. Cuối cùng là ở đây,
            MindX đã và đang và sẽ tiếp tục là priority, cùng những tiền bối dẫn
            dắt tôi hoàn thành <em>Career path</em> của mình.
          </p>
        </section>

        <section>
          <header>
            <h3>Liên hệ</h3>
          </header>
          <form method="post" action="#">
            <div className="row">
              <div className="col-6 col-12-mobilep">
                <label htmlFor="name">Tên của bạn</label>
                <input
                  className="text"
                  type="text"
                  name="name"
                  id="name"
                  defaultValue=""
                  placeholder="Nguyễn Đình A"
                />
              </div>
              <div className="col-6 col-12-mobilep">
                <label htmlFor="email">Email</label>
                <input
                  className="text"
                  type="text"
                  name="email"
                  id="email"
                  defaultValue=""
                  placeholder="anguyend@domain.tld"
                />
              </div>
              <div className="col-12">
                <label htmlFor="subject">Bạn muốn nói gì?</label>
                <textarea
                  name="message"
                  id="message"
                  placeholder="Cứ thoải mái viết ở đây ạ"
                  rows="6"
                ></textarea>
              </div>
              <div className="col-12">
                <ul className="actions special">
                  <li>
                    <input type="submit" value="Gửi cho tôi" />
                  </li>
                  <li>
                    <input type="reset" value="Reset" className="alt" />
                  </li>
                </ul>
              </div>
            </div>
          </form>
        </section>
      </div>
    </div>
  </Layout>
);

export default IndexPage;
